import React, { Component } from "react";
import "./App.css";
import SearchBar from "./SearchBar.js";
import MenuBar from "./MenuBar.js";
import PhotoCarousel from "./PhotoCarousel.js";
import NavBar from "./NavBar.js";
import Home from './Home.js';
import Cooking from './Cooking.js';
import "materialize-css/dist/css/materialize.min.css";
import "./index.css";
import "./Modal.css";
import axios from "axios";

export const DEFAULT_ALBUM = "default";
export const SEARCH_FLAGS = [ "Please login to use this feature.", "No albums found.", "Make sure you're connected to the internet." ];
const Tab = {
  HOME: 1,
  PHOTOS: 2,
  COOKING: 3
};

const TabNames = [ "Photos", "Cooking", "Home Page"];

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      idtoken: "",
      email: "",
      currAlbum: DEFAULT_ALBUM,
      photos: [],
	  searchStatus: "",
	  currentTab: Tab.HOME,
	  pageName: TabNames[2]
    };
  }

  authCallBack = (data) => {
    this.setState({
      idtoken: data.idtoken,
      email: data.email,
	  searchStatus: ""
    });
  }

  removeAuthCallBack = () => {
	this.setState({
      idtoken: "",
      email: "",
	  searchStatus: ""
	});
  }

  setAlbumCallBack = (album) => {
    axios
      .get("/albumPhotos", {
        params: { idtoken: this.state.idtoken, albumName: album },
      })
      .then(
        (resolve) => {
          if (resolve.data[0] !== undefined) {
            this.setState({
              currAlbum: album,
              photos: resolve.data[0].album,
			  searchStatus: ""
            });
          } else {
            this.setState({
              currAlbum: DEFAULT_ALBUM,
              photos: [],
			  searchStatus: SEARCH_FLAGS[1]
            });
          }
        },
        (error) => {
		  if (error.response.status === 400) {
			this.setState({
			  searchStatus: SEARCH_FLAGS[0]
			});
		  } else if (error.response.status === 404) {
			this.setState({
			  searchStatus: SEARCH_FLAGS[2]
			});
		  }

          console.log("Album doesn't exist!");
        }
      );
  }

  clearCallback = () => {
	this.setState({
	  searchStatus: ""
	});
  }

  handleHome = () => {
	this.setState({
	  currentTab: Tab.HOME,
	  pageName: TabNames[2]
	});
  }

  menuBarCallBack = (tab) => {
	let name = null;

	if (tab === TabNames[0]) {
	  tab = Tab.PHOTOS;
	  name = 0;
	} else if (tab === TabNames[1]) {
	  tab = Tab.COOKING;
	  name = 1;
	}

	this.setState({
	  currentTab: tab,
	  pageName: TabNames[name]
	});
  }

  render() {
    return (
      <div className="App" style={{ background: "#FFFAFA" }}>
        <NavBar getAuth={this.authCallBack} removeAuthCallBack={this.removeAuthCallBack}/>
        <br />
        <h1 onClick={this.handleHome} style={{cursor: "pointer", paddingBottom: "1%" }} className="waves-effect" >Stiny's {this.state.pageName}</h1>
        <MenuBar menuBarCallBack={this.menuBarCallBack} />
		{ this.state.currentTab === Tab.PHOTOS ?
		<div>
          <SearchBar setAlbum={this.setAlbumCallBack} searchStatus={this.state.searchStatus} clearCallback={this.clearCallback}/>
          <PhotoCarousel
            idtoken={this.state.idtoken}
            email={this.state.email}
            album={this.state.currAlbum}
            photos={this.state.photos}
        />
		</div> : null
		}
		{ this.state.currentTab === Tab.HOME ?
		  <Home />
		  : null }
		{ this.state.currentTab === Tab.COOKING ?
		  <Cooking />
		  : null }
        <div className="rect"></div>
      </div>
    );
  }
}

export default App;

